.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.background {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-image: url(https://images.unsplash.com/photo-1519003722824-194d4455a60c?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1510&q=80);
  background-size: cover;
  z-index: -10; 
}

.dropHere {
  display: flex;
  width: 40vw;
  height: 60vh;
  font-size: 25px;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  background-color: #8888886c;
  box-shadow: 2px 2px 5px 2px #2e2e2e8e;
}
.dropHere:hover{
  background-color: #888888a6;
}

.waybillCnt{
  color: #333333;
  font-size: 20px;
}

.waybill{
  width: 35vw;
  color: #333333;
  font-size: 15px;
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
  margin: 0;
  margin-left: 10px;
}

.waybillList{
  width: 40vw;
  height: 60vh;
  background-color: #ffffffa4;
  overflow-y: scroll;
}

.item {
  display: block;
}

.item-code {
  display: inline-block;
  width: 200px;
  height: 30px;
  font-size: 20px;
}
.item-nm {
  display: inline-block;
  width: 400px;
  height: 30px;
  font-size: 20px;
}
.item-qty {
  display: inline-block;
  width: 100px;
  height: 30px;
  font-size: 20px;
}

.download {
  width:100px;
  height:50px;
  background:#ff8874;
  border:none;
  margin:3px;
  font-size: 15px;
  font-weight: bold;
  margin-top: 30px;
  padding: 3px;
  color: white;
  cursor: default;
  -ms-user-select: none; 
  -moz-user-select: -moz-none; 
  -webkit-user-select: none; 
  -khtml-user-select: none; 
  user-select:none;
}

.download:hover {
  background:#ffb1a3;
}